import React from 'react';
import Icon from './Icon';
import emailIcon from"../images/email.svg"
import githubIcon from"../images/github.svg"
import linkedinIcon from"../images/linkedin.svg"
import twitterIcon from"../images/twitter.svg"

function ContactMe() {

    return (
        <section className="footer is-primary" id="footer">
            <div className="container">
                <div className="columns is-centered">
                    <Icon src={emailIcon} alt="Email Me" link="mailto: j92pruitt@gmail.com" />
                    <Icon src={githubIcon} alt="My Github" link="https://github.com/j92pruitt" />
                    <Icon src={linkedinIcon} alt="My LinkedIn" link="https://www.linkedin.com/in/joseph-pruitt-588346210/" />
                    <Icon src={twitterIcon} alt="My Twitter" link="https://twitter.com/j92pruitt" />
                </div>
            </div>
        </section>
    );
}

export default ContactMe;