import React from 'react';
import '../styles/AboutMe.css'

function AboutMe() {

    return (
        <section className="section is-medium has-text-centered" id="about-me">
            <div className="container">
                <div className="columns is-centered">
                    <div className="column is-two-thirds">
                        <h2 className="title is-1 is-spaced">
                            About Me
                        </h2>
                        <h3 className="subtitle is-3">
                            I am new to web developing but I spent 8 years studying math where I learned to solve problems and write code to execute my solutions. I know what I know, will admit what I don't and love creating elegantly efficient code and designs.
                        </h3>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default AboutMe;