import React from 'react';
import '../styles/ProjectTile.css'

function ProjectTile(props) {

    return (
        <div className="column is-12-mobile is-half-tablet is-one-third-desktop">
            <figure className="image is-3by2">
                <img className="project-img" src={props.src} alt={props.alt} />
                <figcaption>
                    <h1 className="title is-size-5 is-size-4-widescreen has-text-white">{props.description}</h1>
                    <div className="is-flex">
                        <a href={props.deployed} target="_blank" rel="noreferrer" className="button is-link is-outlined is-rounded has-text-white">
                            Visit Website
                        </a>
                        <a href={props.repo} target="_blank" rel="noreferrer" className="button is-link is-outlined is-rounded has-text-white">
                            GitHub Repo
                        </a>
                    </div>
                </figcaption>
            </figure>
        </div>
    )
}

export default ProjectTile