import React from 'react';
import avatar from '../images/jp-avatar.svg'

function Hero() {
    
    return (
        <section className="hero has-text-centered section">
            <div className="hero-body">
                <div className="container">
                    <div className="columns is-centered">
                        <div className="column">
                            <h2 className="title is-2">Full Stack Web Developer</h2>
                            <img src={avatar} alt="Joseph Pruitt Avatar" />
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default Hero;