import React from 'react';

const buttonStyle = {
    backgroundColor: '#68cdfe'
}

function NavButton(props) {
    return (
        <a className="button is-rounded is-link has-text-weight-medium" style={buttonStyle} href={props.link} onClick={() => props.handleClick(props.text)}>
            {props.text}
        </a>
    )
}

export default NavButton