import React from 'react';
import NavButton from './NavButton';

function Header(props) {
    
    return (
        <header className="container is-fluid columns is-align-content-center justify-content-space-between">

            <div className="column is-3 is-offset-1">
                <h1 className="title is-1 has-text-centered">
                    Joseph Pruitt
                </h1>
            </div>

            <nav className="navbar column is-4 is-offset-3 is-flex is-justify-content-space-between is-align-content-center mt-1">
                <NavButton link="#about-me" text="About Me" handleClick={props.handlePageChange} />
                <NavButton link="#work" text="Work" handleClick={props.handlePageChange} />
                <NavButton link="#contact-me" text="Contact Me" handleClick={props.handlePageChange} />
                <NavButton link="" text="Resume" handleClick={() => {}}/>
            </nav>
        </header>
    );
}

export default Header;