import React from 'react';
import ProjectTile from './ProjectTile';
import mealPlannerImg from '../images/meal-planner.png'
import codeQuizImg from "../images/code-quiz.png"
import dayCalanderImg from "../images/day-calendar.png"
import collectorImg from "../images/the-collector.png"
import techBlogImg from "../images/tech-blog.png"
import weatherImg from "../images/weather-dashboard.png"

function Work() {

    return (
        <section className="section" id="work">
            <div className="container is-narrow has-text-centered">
                <div className="columns is-multiline is-mobile">

                    <ProjectTile src={mealPlannerImg} alt="Meal Planner Thumbnail" description="A Fullstack Weekly Meal Planner" deployed="https://meal-planner-5.herokuapp.com/" repo="https://github.com/xuannga/Meal-Planner" />

                    <ProjectTile src={weatherImg} alt="Weather Dashboard Thumbnail" description="Dashboard using API to fetch real-time weather and forecasts." deployed="https://j92pruitt.github.io/weather-dashboard/" repo="https://github.com/j92pruitt/weather-dashboard" />

                    <ProjectTile src={collectorImg} alt="The Collector Thumbnail" description="App for looking up Marvel Character info." deployed="https://michellecoder.github.io/The-Collector/" repo="https://github.com/michellecoder/The-Collector" />

                    <ProjectTile src={codeQuizImg} alt="Code Quiz Thumbnail" description="Quiz App featuring dynamic content." deployed="https://j92pruitt.github.io/weather-dashboard/" repo="https://github.com/j92pruitt/javascript-quiz" />

                    <ProjectTile src={techBlogImg} alt="Tech Blog Thumbnail" description="A Fullstack Tech Blog" deployed="https://jp-tech-blog.herokuapp.com/" repo="https://github.com/j92pruitt/tech-blog" />

                    <ProjectTile src={dayCalanderImg} alt="Day Calendar Thumbnail" description="Work Day Planner using Moment" deployed="https://j92pruitt.github.io/day-calendar/" repo="https://github.com/j92pruitt/day-calendar" />
                </div>
            </div>
        </section>
    );
}

export default Work;